import { useRouter } from "next/router";
import React from "react";

class Orders extends React.Component {
  state = { orders: [] };

  componentDidMount() {
    this.fetchOrders();
  }

  fetchOrders = () => {
    fetch("http://localhost:3001/get-orders")
      .then(res => res.json())
      .then(res => {
        this.setState(state => ({ ...state, orders: res.orders }));
        console.log("new orders: ", res);
      });
  };

  render() {
    return (
      <div>
        <h3>Orders:</h3>
        <button onClick={this.fetchOrders}>Refresh</button>
        <PageSelector totalPages={NaN} />
        <OrdersTable orders={this.state.orders} />
      </div>
    );
  }
}

const PageSelector = props => (
    <div>
      <span>{props.page}</span>
    </div>
  );

const OrdersTable = props => (
  <table>
    <thead>
      <tr>
        <th>user_email</th>
        <th>date</th>
        <th>value</th>
        <th>currency</th>
        <th>status</th>
      </tr>
    </thead>
    <tbody>
      {props.orders.map((order, key) => (
        <tr key={key}>
          <td>{order.user_email}</td>
          <td>{order.date}</td>
          <td>{order.value}</td>
          <td>{order.currency}</td>
          <td>{order.status}</td>
        </tr>
      ))}
    </tbody>
  </table>
);

export default Orders;
